import axios from "axios";
import { ALL_USERS_FAILURE, ALL_USERS_REQUEST, ALL_USERS_SUCCESS, SEARCH_USERS_FAILURE, SEARCH_USERS_REQUEST, SEARCH_USERS_SUCCESS } from "../Constant/Login";

export const ActionForUser=(object)=>async(dispatch)=>{
    try {
        dispatch({type:ALL_USERS_REQUEST})
   
        const {data}=await axios.get(`${process.env.REACT_APP_URL}/all/data/users`)
        console.log(data.data);
        dispatch({type:ALL_USERS_SUCCESS,payload:data.data})
         
    } catch (error) {
        dispatch({type:ALL_USERS_FAILURE,payload:error})
        
    }

}
export const ActionForUserSearch=(object)=>async(dispatch)=>{
    try {
        dispatch({type:SEARCH_USERS_REQUEST})
   
        const {data}=await axios.post(`${process.env.REACT_APP_URL}/search/data`,object)
        console.log(data.data);
        dispatch({type:SEARCH_USERS_SUCCESS,payload:data.data})
         
    } catch (error) {
        dispatch({type:SEARCH_USERS_FAILURE,payload:error})
        
    }

}