import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { ActionForUser, ActionForUserSearch } from '../Redux/Action/AllUsers';
import TableAllUsers from './TableAllUsers';
function AllUsers() {

  const dispatch = useDispatch()
  const [input1, setinput1] = useState('')
  const { AllUsersDatas } = useSelector(state => state.AllUser)
  const [dataAll, setdataAll] = useState([])
  useEffect(() => {
    dispatch(ActionForUser())
    setdataAll(AllUsersDatas)
  }, [dispatch])
 const HandleSubmit=(e)=>{
 e.preventDefault()
 dispatch(ActionForUserSearch({input1}))
 }
  return (

    <div className="container-fluid">
      <div className="row mt-5 pt-5">
        <div className="col-lg-10 offset-lg-1">
          <div className="card shadow-lg  border-0">
            <div className="card-body table-responsive">
              <form onSubmit={HandleSubmit}>
              <input type="text" onChange={(e)=>{
                 setinput1(e.target.value)
              }} placeholder='Search client By account no' className="form-control my-2" /> <button className="btn btn-dark mx-1">Search</button>
              </form>
             {
               <TableAllUsers data={AllUsersDatas} />
             }
            </div>
          </div>
        </div>
      </div>
      
    </div>
  )
}

export default AllUsers


